.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		padding: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 10rem 0 0;
		background-color: var(--color-primary);
		color: var(--color-dark);
		@include max-md {
			padding: 8rem 0 0;
			.container-couple {
				padding: 0;
			}
		}
		.ornaments-wrapper {
			z-index: 3;
			.orn-1 {
				position: absolute;
				top: 0;
				left: 0;
				width: 80px;
				transform: translate(-15%, -30%) rotate(-80deg) scaleY(-1);
				@include min-md {
					width: 120px;
				}
				@include min-lg {
					width: 140px;
				}
			}
			.orn-2 {
				position: absolute;
				top: 1rem;
				left: 0;
				width: 100px;
				transform: translate(-20%, 0);
				@include min-md {
					left: 15%;
					width: 120px;
				}
			}
			.orn-3 {
				position: absolute;
				top: 0;
				right: 0;
				width: 160px;
				transform: translate(50%, -30%) scaleY(-1);
				@include min-md {
					width: 220px;
				}
				@include min-lg {
					width: 240px;
				}
				.orn-4 {
					position: absolute;
					bottom: 0;
					left: 0;
					width: 140px;
					transform: translate(-10%, -15%) rotate(-50deg);
					@include min-md {
						width: 180px;
					}
					@include min-lg {
						width: 200px;
					}
				}
			}
			.orn-5 {
				position: absolute;
				top: 0;
				left: 0;
				width: 160px;
				transform: translate(-30%, 0);
				@include min-md {
					width: 180px;
				}
				@include min-lg {
					width: 200px;
				}
				.image-element {
					height: auto;
				}
			}
			.orn-6 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 140px;
				transform: translate(30%, 0) rotate(-60deg) scale(-1);
				@include min-md {
					width: 160px;
				}
				@include min-lg {
					width: 180px;
				}
				.image-element {
					height: auto;
				}
			}
			.orn-7 {
				position: absolute;
				top: 0;
				left: 0;
				width: 30%;
				transform: translate(-20%, 30%);
				@include max-lg {
					width: 40%;
				}
				@include max-md {
					width: 60%;
					transform: translate(-40%, 50%);
				}
			}
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		text-align: left;
		margin-bottom: 3rem;
		z-index: 5;
		.title-section {
			text-align: left;
		}
	}
	.couple {
		position: relative;
		padding: 5rem 0;
		text-align: center;
		background-color: var(--color-main);
		mask-image: url(../images/frame-mask.png);
		mask-size: 100% 100%;
		mask-repeat: no-repeat;
		margin-bottom: -5rem;
		z-index: 10;
		@include max-md {
			padding: 3rem 0;
			&.man {
				margin-bottom: 0;
			}
		}
		.frame {
			width: 103%;
			height: 102%;
		}
		.image-wrap {
			width: 60%;
			position: relative;
			aspect-ratio: 3/4;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				z-index: 5;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 300px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem 1rem 0;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
