.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.ornaments-wrapper {
		z-index: 3;
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(-38%, -30%) scaleY(-1);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			.orn-2 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 120px;
				transform: translate(30%, -20%);
				@include min-md {
					width: 160px;
				}
				@include min-lg {
					width: 180px;
				}
			}
		}
		.orn-3 {
			position: absolute;
			top: 0;
			right: 0;
			width: 160px;
			transform: translate(40%, -30%) scaleY(-1);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			.orn-4 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100px;
				transform: translate(35%, -5%) scaleX(-1);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
			}
		}
		.orn-5 {
			position: absolute;
			top: 0;
			left: 50%;
			width: 120px;
			transform: translate(-50%, -50%) scaleY(-1);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
		}
		.orn-6 {
			position: absolute;
			top: 50%;
			left: 0;
			width: 160px;
			transform: translate(-68%, -50%);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
		}
		.orn-7 {
			position: absolute;
			top: 50%;
			right: 0;
			width: 160px;
			transform: translate(65%, -50%);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 220px;
			}
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 160px;
			transform: translate(-36%, 32%);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			.orn-9 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100px;
				transform: translate(30%, -20%);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
			}
		}
		.orn-10 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 160px;
			transform: translate(36%, 30%);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			.orn-11 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 140px;
				transform: translate(-5%, -10%);
				@include min-md {
					width: 180px;
				}
				@include min-lg {
					width: 200px;
				}
			}
		}
		.orn-12 {
			position: absolute;
			top: 10%;
			left: 10%;
			width: 80px;
			@include min-md {
				left: 20%;
				width: 100px;
			}
		}
		.orn-13 {
			position: absolute;
			top: 25%;
			right: 10%;
			width: 60px;
			@include min-md {
				right: 20%;
				width: 80px;
			}
		}
		.orn-14 {
			position: absolute;
			bottom: 20%;
			left: 0;
			width: 100px;
			transform: translate(-10%, 0);
			@include min-md {
				left: 10%;
				width: 120px;
			}
		}
		.orn-15 {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 100px;
			transform: translate(-50%, -20%);
			@include min-md {
				width: 120px;
			}
		}
	}
	.header-content {
		position: relative;
		height: 100vh;
		.header {
			position: relative;
			width: 35%;
			height: 100%;
			margin: 0 auto;
			padding: 5rem 0 4rem;
			@include max-xl {
				width: 40%;
			}
			@include max-lg {
				width: 70%;
			}
			@include max-md {
				width: 100%;
			}
			.header-title {
				position: relative;
				text-align: center;
				display: flex;
				justify-content: center;
				align-items: center;
				width: 100%;
				height: 100%;
				@include max-lg {
					height: 80%;
				}
				@include max-md {
					min-height: 80%;
					height: auto;
				}
				&-content {
					position: relative;
				}
				.elements-wrapper-absolute {
					mask-image: url(../images/frame-mask.png);
					mask-size: 100% 100%;
					mask-repeat: no-repeat;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 80%;
				height: auto;
				aspect-ratio: 4/6;
				margin: 0 auto;
				align-self: flex-end;
				.header-background {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
				.image-element {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: 90%;
					height: 93%;
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
