.thank-section {
	position: relative;
	@include max-md {
		.image-container {
			padding: 0;
		}
	}
	.frame {
		width: 104%;
	}
	.title-section {
		text-align: left;
	}
	.image-wrapper {
		position: relative;
		width: 90%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0.5rem auto 0;
		z-index: 3;
		@include max-md {
			width: 100%;
			margin-top: -1.6rem;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				mask-image: url(../images/frame-mask.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		padding: 0 0 5rem;
		.orn-1 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 160px;
			transform: translate(-36%, 32%);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			.orn-2 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100px;
				transform: translate(30%, -20%);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
			}
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 160px;
			transform: translate(36%, 30%);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			.orn-4 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 140px;
				transform: translate(-5%, -10%);
				@include min-md {
					width: 180px;
				}
				@include min-lg {
					width: 200px;
				}
			}
		}
		.orn-5 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(-50%, -45%) scale(-1);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			.orn-6 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 140px;
				transform: translate(0, -20%) rotate(-20deg) scaleX(-1);
				@include min-md {
					width: 180px;
				}
				@include min-lg {
					width: 200px;
					transform: translate(0, -15%) rotate(-20deg) scaleX(-1);
				}
			}
		}
		.orn-7 {
			position: absolute;
			top: 0;
			right: 10%;
			width: 80px;
			transform: translate(0, -20%);
			z-index: 3;
			@include min-md {
				width: 100px;
				transform: translate(0, -80%);
			}
		}
		.orn-8 {
			position: absolute;
			top: 0;
			right: 0;
			width: 100%;
			transform: translate(50%, 0);
			@include min-md {
				width: 50%;
				transform: translate(30%, -40%);
			}
			@include min-lg {
				width: 40%;
			}
		}
		.thank {
			position: relative;
			padding: 3rem 2rem;
			text-align: left;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
