.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-30%, -5%);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
		.orn-2 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 140px;
			transform: translate(10%, -5%);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 160px;
		transform: translate(20%, 0);
		@include min-md {
			width: 220px;
			transform: translate(25%, 0);
		}
		@include min-lg {
			width: 240px;
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(-10%, 0);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		top: 1rem;
		right: 0;
		width: 60px;
		transform: translate(10%, 0);
		z-index: 3;
		@include min-md {
			width: 80px;
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 20%;
		left: 0;
		width: 80px;
		transform: translate(-40%, 0);
		z-index: 3;
		@include min-md {
			width: 100px;
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		height: 100vh;
		@include max-lg {
			padding: 4rem 0 6.5rem;
			align-items: center;
		}
		.cover-content {
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				width: 30%;
				min-height: 80%;
				color: var(--color-dark);
				@include max-xl {
					width: 40%;
				}
				@include max-lg {
					width: 70%;
				}
				@include max-md {
					width: 100%;
				}
				&::after {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background-color: var(--color-primary);
					mask-image: url(../images/frame-mask.png);
					mask-size: 100% 100%;
					mask-repeat: no-repeat;
					box-shadow: inset 0 0 1rem rgba(0, 0, 0, 0.15);
					content: "";
				}
				.greeting-wrapper {
					position: relative;
					margin-bottom: 1rem;
				}
				h6 {
					font-weight: 700;
					margin-bottom: 0;
				}
				.cover-description {
					position: relative;
					width: 100%;
					padding: 2rem;
					text-align: center;
					z-index: 5;
					@include max-lg {
						padding: 2rem 1rem;
					}
					.title {
						padding: 1rem 0;
						line-height: 1.2;
					}
					p {
						margin-bottom: 0;
					}
					.cover-event {
						position: relative;
						margin-bottom: 1rem;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
