.reminder-wrap {
	position: relative;
	display: block;
	@include max-md {
		.container {
			padding: 0;
		}
	}
	.reminder-content {
		position: relative;
		width: 100%;
		padding: 8rem 3rem 3rem 20%;
		@include max-md {
			padding-left: 2rem;
		}
		.ornaments-wrapper {
			z-index: 10;
			.orn-1 {
				position: absolute;
				top: 0;
				right: 0;
				width: 160px;
				transform: translate(40%, 15%) scaleY(-1);
				@include min-md {
					width: 220px;
				}
				@include min-lg {
					width: 240px;
					transform: translate(40%, 0) scaleY(-1);
				}
				.orn-2 {
					position: absolute;
					bottom: 0;
					left: 0;
					width: 100px;
					transform: translate(20%, 10%) rotate(-45deg);
					@include min-md {
						width: 140px;
					}
					@include min-lg {
						width: 160px;
					}
				}
			}
			.orn-3 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 160px;
				transform: translate(40%, 30%);
				@include min-md {
					width: 220px;
				}
				@include min-lg {
					width: 240px;
				}
				.orn-4 {
					position: absolute;
					bottom: 0;
					left: 0;
					width: 140px;
					@include min-md {
						width: 180px;
					}
					@include min-lg {
						width: 200px;
					}
				}
			}
			.orn-5 {
				position: absolute;
				top: 18%;
				left: 0;
				width: 80px;
				@include min-md {
					left: 10%;
					width: 100px;
				}
			}
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		display: flex;
		align-items: center;
		margin: 0;
		margin-right: auto;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
