.rsvp-section {
	position: relative;
	padding: 3rem 0 5rem;
	@include max-md {
		padding: 3rem 0;
		.container {
			padding: 0;
		}
	}
	.orn-99 {
		position: relative;
		width: 60px;
		margin: 0 auto 1rem;
		@include min-md {
			width: 80px;
		}
	}
	.ornaments-wrapper {
		z-index: 3;
		.orn-1 {
			position: absolute;
			top: 0rem;
			left: 0;
			width: 140px;
			transform: translate(-40%, -10%) scaleY(-1);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
			.orn-2 {
				position: absolute;
				top: 0;
				left: 0;
				width: 100px;
				transform: translate(30%, 70%) rotate(40deg) scaleX(-1);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
			}
		}
		.orn-3 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: translate(40%, -40%);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
			.orn-4 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 100px;
				transform: translate(-30%, 50%) rotate(30deg) scaleY(-1);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
			}
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 160px;
			transform: translate(-50%, 20%);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			.orn-6 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 140px;
				transform: translate(-10%, 0) rotate(10deg);
				@include min-md {
					width: 180px;
				}
				@include min-lg {
					width: 200px;
				}
			}
		}
		.orn-7 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 160px;
			transform: translate(50%, 20%);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			.orn-8 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 140px;
				transform: rotate(-30deg);
				@include min-md {
					width: 180px;
				}
				@include min-lg {
					width: 200px;
				}
			}
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 3rem 3rem 8rem;
		@include max-md {
			padding: 2rem 2rem 5rem;
		}
		.elements-wrapper-absolute {
			mask-image: url(../images/frame-mask.png);
			mask-size: 100% 100%;
			mask-repeat: no-repeat;
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
		}
	}
}
